'use strict';

/*
* When adding modules to the namespace, ensure that they are added in alphabetical order according to module name.
*/

// MODULES //

import setReadOnly from '@stdlib/utils/define-read-only-property' ;


// MAIN //

/**
* Top-level namespace.
*
* @namespace ns
*/
var ns = {};

/**
* @name constants
* @memberof ns
* @readonly
* @type {Namespace}
* @see {@link module:@stdlib/constants/time}
*/
import constants from '@stdlib/constants/time';
setReadOnly( ns, 'constants', constants );

/**
* @name dayOfQuarter
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/time/day-of-quarter}
*/
import dayOfQuarter from './../day-of-quarter';
setReadOnly( ns, 'dayOfQuarter', dayOfQuarter );

/**
* @name dayOfYear
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/time/day-of-year}
*/
import dayOfYear from './../day-of-year';
setReadOnly( ns, 'dayOfYear', dayOfYear );

/**
* @name daysInMonth
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/time/days-in-month}
*/
import daysInMonth from './../days-in-month';
setReadOnly( ns, 'daysInMonth', daysInMonth );

/**
* @name daysInYear
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/time/days-in-year}
*/
import daysInYear from './../days-in-year';
setReadOnly( ns, 'daysInYear', daysInYear );

/**
* @name hoursInMonth
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/time/hours-in-month}
*/
import hoursInMonth from './../hours-in-month';
setReadOnly( ns, 'hoursInMonth', hoursInMonth );

/**
* @name hoursInYear
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/time/hours-in-year}
*/
import hoursInYear from './../hours-in-year';
setReadOnly( ns, 'hoursInYear', hoursInYear );

/**
* @name isoWeeksInYear
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/time/iso-weeks-in-year}
*/
import isoWeeksInYear from './../iso-weeks-in-year';
setReadOnly( ns, 'isoWeeksInYear', isoWeeksInYear );

/**
* @name minutesInMonth
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/time/minutes-in-month}
*/
import minutesInMonth from './../minutes-in-month';
setReadOnly( ns, 'minutesInMonth', minutesInMonth );

/**
* @name minutesInYear
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/time/minutes-in-year}
*/
import minutesInYear from './../minutes-in-year';
setReadOnly( ns, 'minutesInYear', minutesInYear );

/**
* @name now
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/time/now}
*/
import now from './../now';
setReadOnly( ns, 'now', now );

/**
* @name quarterOfYear
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/time/quarter-of-year}
*/
import quarterOfYear from './../quarter-of-year';
setReadOnly( ns, 'quarterOfYear', quarterOfYear );

/**
* @name secondsInMonth
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/time/seconds-in-month}
*/
import secondsInMonth from './../seconds-in-month';
setReadOnly( ns, 'secondsInMonth', secondsInMonth );

/**
* @name secondsInYear
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/time/seconds-in-year}
*/
import secondsInYear from './../seconds-in-year';
setReadOnly( ns, 'secondsInYear', secondsInYear );

/**
* @name tic
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/time/tic}
*/
import tic from './../tic';
setReadOnly( ns, 'tic', tic );

/**
* @name toc
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/time/toc}
*/
import toc from './../toc';
setReadOnly( ns, 'toc', toc );


// EXPORTS //

export default ns;
